<!-- 登录页面 -->

<template>
    <div class="login">
        <div class="box">
            <span class="table">编程培训视频网</span><br>
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="昵称" prop="name">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input v-model="form.password"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">登录</el-button>
                    <el-button @click="resetForm('form')">重置</el-button>
                    <el-button type="primary" @click="push('/Register')">注册</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
const options = {
    data() {
        return {
            form: {
                name: '',
                password: ''
            }
        }
    },
    methods: {
        push(url) {
            this.$router.push(url);
        },
        onSubmit() {
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/user/login",
                data: this.form,
                method: 'post',
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {
                    if (res.data == 2) {
                        this.$message("管理员登录成功，即将跳转");
                        this.push('/AdminMenu');
                    } else if (res.data == 1) {
                        this.$message("用户登录成功，即将跳转");
                        this.$store.commit('saveName', this.form.name);
                        this.push('/UserMenu');
                    } else if (res.data == 0) {
                        this.$message('密码错误');
                        this.resetForm('form');
                    } else {
                        this.$message("未找到该用户");
                        this.resetForm('form');
                    }
                });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        }
    }
}
export default options;
</script>
<style scoped>
.login {
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 0;
    text-decoration: none;
    height: 100vh;
    background-size: cover;
    width: 550px;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to right bottom,
            rgba(255, 255, 255, .7),
            rgba(255, 255, 255, .5),
            rgba(255, 255, 255, .4));
    /* 使背景模糊化 */
    backdrop-filter: blur(10px);
    box-shadow: 0 0 20px #a29bfe;
    border-radius: 15px;
}

.table {
    font: 900 40px '';
    text-align: center;
    letter-spacing: 5px;
    color: #3d3d3d;
}

.box {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.box input {
    width: 400px;
    height: 100%;
    margin-bottom: 20px;
    outline: none;
    border: 0;
    padding: 10px;
    background-color: transparent;
    border-bottom: 3px solid rgb(150, 150, 240);
    font: 900 16px '';
    justify-content: center;
    align-items: center;
}</style>